.enter {
  opacity: 0;
  transform: translateX(-150%);
}

.enterActive {
  opacity: 1;
  transform: translateX(0%);
  transition: all 250ms ease-in;
}

.exit {
  transform: translateX(0%);
  opacity: 1;
}

.exitActive {
  opacity: 0;
  transform: translateX(-150%);
  transition: all 250ms ease-out;
}
